.solarSystem {
    display: flex;
    justify-content: space-between;
    height: 100vh;
    background-color: #000;
    color: white;
}

.planetMenu {
    width: 20%;
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.planetButton {
    padding: 10px;
    margin-bottom: 10px;
    background-color: #333;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    text-align: center;
    transition: background-color 0.3s ease;
}

.planetButton:hover {
    background-color: #555;
}

.panel {
    width: 75%;
    padding: 20px;
    background-color: #1a1a1a;
    transition: transform 0.5s ease;
}

.panel h1 {
    font-size: 2.5em;
}

.panel p {
    font-size: 1.2em;
}

.planetImage {
    width: 100%;
    height: auto;
    margin-top: 20px;
    border-radius: 10px;
}

.closeBig {
    position: absolute;
    right: 60px;
    opacity: 0;
    transition: all 0.3s 0.4s;
    z-index: 3;
    top: 65px;
}

.panel img {
    border-radius: 2px;
    width: 100%;
}